import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccessoriesListComponent } from "./accessories-list/accessories-list.component";
import { AccessoriesAddComponent } from "./accessories-add/accessories-add.component";


const routes: Routes = [
  { path: 'list', component: AccessoriesListComponent },
  { path: 'add', component: AccessoriesAddComponent },
  { path: 'edit:id', component: AccessoriesAddComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccessoriesRoutingModule { }
