import { Component, OnInit } from '@angular/core';
import { MessageService, ConfirmationService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { NgDropDownOption } from '../../mobiles/models/ng-dropdown-option.model';
import { TableColumn } from '../../mobiles/models/table.columns.model';
import { MobileApiService } from '../../mobiles/services/mobile-api.service';
import { AccessoriesAddComponent } from '../accessories-add/accessories-add.component';
import { AccessoriesVersionAddComponent } from '../accessories-version-add/accessories-version-add.component';
import { Accessories, AccessoriesGrouped, AccessoriesVersion } from "../models/accessories";
import { AccessoriesService } from "../services/accessories.service";

@Component({
  selector: 'app-accessories-list',
  templateUrl: './accessories-list.component.html',
  styleUrls: ['./accessories-list.component.scss'],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class AccessoriesListComponent implements OnInit {
  mobileModelOptions: NgDropDownOption[] = [];
  accessoryType: NgDropDownOption[] = [];
  deviceBrandOptions: NgDropDownOption[] = [];

  cols: TableColumn[] = [];
  Accessories: Accessories[] = [];
  selecteAccessory: Accessories

  constructor(
    private dialogService: DialogService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private mobileApiSrvc: MobileApiService,
    private AccessoriesServices: AccessoriesService
  ) {
  }

  ngOnInit(): void {
    this.getTableColumns()
    this.getAllAccessoriesData()
  }

  getTableColumns() {
    let columns: string[] = ["Name", "Description", "VideoUrl", "Brand", 'Model']
    columns.forEach(column => this.cols.push(new TableColumn(column, column)))
  }

  getDeviceBrand() {
    let deviceBrands: string[] = []
    this.mobileApiSrvc.getDeviceBrand().subscribe(data => {
      deviceBrands = data;
      deviceBrands.forEach(deviceBrand => this.deviceBrandOptions.push(new NgDropDownOption(deviceBrand['id'], deviceBrand['name'])))
    })
  }

  getAllAccessoriesData() {
    this.AccessoriesServices.getAllAccessories().subscribe((res: AccessoriesGrouped) => {
      this.Accessories = res.data;
    }
    )
  }

  openNew(mode: string, data?) {
    const ref = this.dialogService.open(AccessoriesAddComponent, {
      data: {
        data,
        mode: mode
      },
      header: `${mode} Accessories`,
      width: '50%',
      contentStyle: { "max-height": "500px", "overflow": "auto" }
    });

    ref.onClose.subscribe(data => {

      if (data) {
        this.messageService.add({ severity: 'Success', summary: 'Accessories Action', detail: data, life: 3000 });
        this.getAllAccessoriesData();
      }
    })
  }

  deleteAccessory(Accessory: Accessories) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete ' + Accessory.name + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.Accessories = this.Accessories.filter(Acce => Acce.id !== Accessory.id);
        this.AccessoriesServices.deleteAccessory(Accessory.id).subscribe(data => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'Accessory Deleted', life: 3000 });
        })
      }
    });
  }

  manageVersion(mode, accessoryId?, data?) {
    const ref = this.dialogService.open(AccessoriesVersionAddComponent, {
      data: {
        data,
        mode,
        accessoryId
      },
      header: `${mode} Version`,
      width: '50%',
      contentStyle: { "max-height": "500px", "overflow": "auto" },
    });

    ref.onClose.subscribe(data => {
      if (data) {
        this.getAllAccessoriesData()
        this.messageService.add({ severity: 'Success', summary: 'Version Action', detail: data, life: 3000 });
      }
    })
  }

  deleteVersion(version: AccessoriesVersion) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete this item ?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.AccessoriesServices.deleteversion(version.id).subscribe(data => {
          this.getAllAccessoriesData();
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'Version Deleted', life: 3000 });
        })
      }
    });
  }
}
