import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Accessories, AccessoriesGrouped, AccessoriesVersion } from '../models/accessories';

@Injectable({
  providedIn: 'root'
})
export class AccessoriesService {

  private prefix: string = '/Accessory'

  constructor(private http: HttpClient) { }


  getAccessoryType() {
    return this.http.get(`${environment.api}/AccessoryType`).pipe(map(res => {
      return res['data']
    }))
  }

  getAllAccessories() {
    return this.http.get(`${environment.api}/${this.prefix}`).pipe((map((Accessories: AccessoriesGrouped) => {
      return Accessories;
    })))
  }


  addAccessory(Accessory: Accessories) {
    Accessory['versions'] = JSON.stringify(Accessory['versions'])
    return this.http.post(`${environment.api}/${this.prefix}`, Accessory).pipe(map(res => {
      return res
    }))
  }


  editAccessory(AccessoryId: number, newData: Accessories) {
    return this.http.put(`${environment.api}/${this.prefix}/${AccessoryId}`, newData).pipe(map(res => {
      return res
    }))
  }

  deleteAccessory(AccessoryId: number) {
    return this.http.delete(`${environment.api}/${this.prefix}/${AccessoryId}`).pipe(map(res => {
      return res
    }))
  }

  /**             Version              */

  addVersion(version: AccessoriesVersion) {  
    return this.http.post(`${environment.api}/AccessoryVersion`, version).pipe(map(res => {
      return res
    }))
  }

  updateVersion(version: AccessoriesVersion) {
    return this.http.put(`${environment.api}/AccessoryVersion/${version.id}`, version).pipe(map(res => {
      return res
    }))
  }

  deleteversion(versionId: number) {
    return this.http.delete(`${environment.api}/AccessoryVersion/${versionId}`).pipe(map(res => {
      return res
    }))
  }

}
