import { Component, OnInit, Version } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { NgDropDownOption } from '../../mobiles/models/ng-dropdown-option.model';
import { AccessoriesVersion } from '../models/accessories';
import { AccessoriesService } from '../services/accessories.service';

@Component({
  selector: 'app-accessories-version-add',
  templateUrl: './accessories-version-add.component.html',
  styleUrls: ['./accessories-version-add.component.scss']
})
export class AccessoriesVersionAddComponent implements OnInit {
  versionForm: FormGroup;
  mode: string;
  data: AccessoriesVersion;
  highlightOptions: NgDropDownOption[] = [];
  hotDealOptions: NgDropDownOption[] = [];

  accessoryId: number = 0
  constructor(
    public ref: DynamicDialogRef,
    public config: DynamicDialogConfig,
    private AccessoriesService: AccessoriesService,
  ) { }

  ngOnInit(): void {
    this.createVersionForm();
    this.getHighlight();
    this.gethotDeal();

    this.mode = this.config.data.mode;
    this.accessoryId = this.config.data.accessoryId;
    this.data = this.config.data.data;

    if (this.mode !== "Add" && this.data) {
      this.versionForm.patchValue(this.data);
    }
  }

  createVersionForm() {
    this.versionForm = new FormGroup({
      productImage: new FormControl('', [Validators.required]),
      discountPrice: new FormControl('', []),
      price: new FormControl('', [Validators.required]),
      quantity: new FormControl('', [Validators.required]),
      style: new FormControl('', [Validators.required]),
      highlight: new FormControl('', [Validators.required]),
      hotDeal: new FormControl('', [Validators.required]),
    })
  }

  getHighlight() {
    this.highlightOptions.push({ label: 'True', value: true })
    this.highlightOptions.push({ label: 'False', value: false })
  }

  gethotDeal() {
    this.hotDealOptions.push({ label: 'True', value: true })
    this.hotDealOptions.push({ label: 'False', value: false })
  }

  ImageBaseData: any;

  selectImage(event) {
    let reader = new FileReader();
    reader.readAsDataURL(event.currentFiles[0]);
    reader.onload = () => {
      this.ImageBaseData = reader.result;
      this.ImageBaseData = this.ImageBaseData.split(',')[1];
      this.versionForm.patchValue(
        { productImage: this.ImageBaseData }
      )
    };

    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  handleVersion() {

    if (!this.versionForm.valid) {
      return this.versionForm.markAllAsTouched();
    }

    if (this.mode === "Add") {
      this.addVersion(this.accessoryId, this.versionForm.value);
    }
    else {
      this.editVersion(this.accessoryId, this.versionForm.value);
    }

  }

  addVersion(accessoryId: number, version: AccessoriesVersion) {
    version['accessoryId'] = accessoryId
    console.log(version, 'version')
    this.AccessoriesService.addVersion(version).subscribe(data => {
      this.ref.close('Version Added')
    })

  }


  editVersion(accessoryId: number, version: AccessoriesVersion) {
    version['accessoryId'] = accessoryId
    version['id'] = this.data['id']
    this.AccessoriesService.updateVersion(version).subscribe(data => {
      this.ref.close('Version Edited ')
    })
  }


}
