import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { MessageService } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { NgDropDownOption } from '../../mobiles/models/ng-dropdown-option.model';
import { MobileApiService } from '../../mobiles/services/mobile-api.service';
import { AccessoriesService } from '../services/accessories.service';

@Component({
  selector: 'app-accessories-add',
  templateUrl: './accessories-add.component.html',
  styleUrls: ['./accessories-add.component.scss']
})
export class AccessoriesAddComponent implements OnInit {
  mode: string;
  data: {};
  deviceBrandOptions: NgDropDownOption[] = [];
  AccessoryTypeOptions: NgDropDownOption[] = [];
  ModelsOptions: NgDropDownOption[] = [];
  highlightOptions: NgDropDownOption[] = [];
  hotDealOptions: NgDropDownOption[] = [];
  accessoryForm: FormGroup;
  constructor(
    private messageService: MessageService,
    public ref: DynamicDialogRef,
    public config: DynamicDialogConfig,
    private AccessoriesService: AccessoriesService,
    private MobileApiService: MobileApiService
  ) { }

  async ngOnInit() {
    this.mode = this.config.data.mode;
    this.createAccessoryForm();
    await this.getDeviceBrand();
    await this.getAccessoryType();
    await this.getMobileMode();
    this.getHighlight();
    this.gethotDeal();
    this.data = this.config.data.data ? this.config.data.data : null;
    if (this.data) {
      console.log(this.data)
      this.accessoryForm.patchValue(this.data);
      // this.accessoryForm.patchValue({
      //   name: this.data['name'],
      //   videoUrl: this.data['videoUrl'],
      //   description: this.data['description'],
      //   mobileBrandId: this.data['mobileBrandId'],
      //   accessoryTypeId: this.data['accessoryTypeId'],
      //   mobileModelId: this.data['mobileModelId']
      // })
    }
  }

  createAccessoryForm() {
    this.accessoryForm = new FormGroup({
      name: new FormControl('', [Validators.required]),
      description: new FormControl('', []),
      videoUrl: new FormControl('', []),
      accessoryTypeId: new FormControl('', [Validators.required]),
      mobileBrandId: new FormControl('', []),
      mobileModelId: new FormControl('', []),
    })

    if (this.mode === "Add") {
      this.accessoryForm.addControl("versions", new FormArray([this.createVersion()], [Validators.required]))
    }
    // versions: new FormArray([this.createVersion()], [Validators.required])
  }


  async getDeviceBrand() {
    let deviceBrands: string[] = []
    await this.MobileApiService.getDeviceBrand().toPromise()
      .then(data => {
        deviceBrands = data;
        deviceBrands.forEach((deviceBrand, index) => this.deviceBrandOptions.push({ label: deviceBrand['name'], value: deviceBrand['id'] }))
      })
  }

  async getAccessoryType() {
    let accessoryTypes: string[] = []
    await this.AccessoriesService.getAccessoryType().toPromise()
      .then(data => {
        accessoryTypes = data;
        accessoryTypes.forEach((accessoryType, index) => this.AccessoryTypeOptions.push({ label: accessoryType['name'], value: accessoryType['id'] }))
      })
  }

  async getMobileMode() {
    let mobileModes: string[] = []
    await this.MobileApiService.getMobileModel().toPromise()
      .then(data => {
        mobileModes = data;
        mobileModes.forEach((mobileMode, index) => this.ModelsOptions.push({ label: mobileMode['name'], value: mobileMode['id'] }))
      })
  }

  getHighlight() {
    this.highlightOptions.push({ label: 'True', value: true })
    this.highlightOptions.push({ label: 'False', value: false })
  }

  gethotDeal() {
    this.hotDealOptions.push({ label: 'True', value: true })
    this.hotDealOptions.push({ label: 'False', value: false })
  }

  ImageBaseData: any;

  selectImage(event) {
    let reader = new FileReader();
    reader.readAsDataURL(event.currentFiles[0]);
    reader.onload = () => {
      this.ImageBaseData = reader.result;
      this.ImageBaseData = this.ImageBaseData.split(',')[1];
      this.Versions.patchValue(
        [{ productImage: this.ImageBaseData }]
      )
    };

    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  get Versions() {
    const items = this.accessoryForm.get('versions') as FormArray;
    return this.accessoryForm.get('versions') as FormArray;
  };

  createVersion(data?): FormGroup {
    return new FormGroup({
      productImage: new FormControl(data ? data.productImage : '', [Validators.required]),
      discountPrice: new FormControl(data ? data.discountPrice : '', []),
      price: new FormControl(data ? data.price : '', [Validators.required]),
      quantity: new FormControl(data ? data.quantity : '', [Validators.required]),
      style: new FormControl(data ? data.style : '', [Validators.required]),
      highlight: new FormControl('', [Validators.required]),
      hotDeal: new FormControl('', [Validators.required]),
    });
  }

  addNewVesion() {
    this.Versions.push(this.createVersion());
  }

  deleteVersion(i) {
    this.Versions.removeAt(i);
  }

  handleAccessory() {
    if (!this.accessoryForm.valid) {
      this.accessoryForm.markAllAsTouched();
      return this.messageService.add({ severity: 'Error', summary: 'Required Field', detail: 'Required Input', life: 3000 });
    }

    if (this.mode === "Add") {
      this.AccessoriesService.addAccessory(this.accessoryForm.value).subscribe(data => {
        this.ref.close('Accessory Added');
      })
    } else if (this.mode === "Edit") {
      let accessoryEdited = this.accessoryForm.value;
      accessoryEdited['id'] = this.data['id'];
      this.AccessoriesService.editAccessory(this.data['id'], this.accessoryForm.value).subscribe(data => {
        this.ref.close('Accessory Edited');
      })
    }
  }

}
